.avatar {
  width: 40px;
  height: 40px;
  background-color: rgba(126, 87, 194, 0.1);
  /* light purple */
  color: #673ab7;
  /* purple text */
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  margin-left: 8px;
  overflow: hidden;
  /* ensures image stays within circle */
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* crops & centers image */
  object-position: top;
  display: block;
  /* removes inline gap below img */
  border-radius: 50%;
  /* circular crop */
}

.invite-text {
  color: var(--Text, #211b3a);
  font-family: Urbanist;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 28px;
  margin: 0px;
}
.membership-status {
  border-radius: 6px;
  border: 1px solid #3c9718;
  background: #fff;
  border-radius: 6px;
  padding: 6px 12px;
  color: var(--Success-Source, #3c9718);
  font-family: Urbanist;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: var(--Line-Heights-10, 20px); /* 166.667% */
  letter-spacing: var(--Letter-Spacing-4, 0.25px);
}

.recent-post-module,
.upcoming-events-module,
.all-post-component {
  border-radius: 12px;
  background: #fff;
  padding: 20px;
  h1 {
    color: var(--Text, #211b3a);
    font-family: Urbanist;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    margin: 0px;
  }
  h4 {
    color: #007bff;
    font-family: Urbanist;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 162%;
    margin: 0px;
    cursor: pointer;
  }
}

.left-side-modules {
  width: 60%;
}
.right-side-modules {
  width: 40%;
}

.offcanvas {
  width: 420px; // width of the drawer
}

.upload-box {
  border-style: dashed;
  cursor: pointer;
  transition: background 0.3s;
}

.upload-box:hover {
  background: #f8f9fa;
}

.form-label {
  color: var(--Text-Dark, #212121);
  font-family: Urbanist;
  font-size: var(--Font-Size-2, 14px);
  font-style: normal;
  font-weight: 500;
  line-height: var(--Line-Heights-10, 20px);
  /* 142.857% */
  letter-spacing: var(--Letter-Spacing-2, 0.1px);
}

.upload-wrapper {
  .upload-box {
    display: block;
    border: 2px dashed #afafaf;
    border-radius: 10px;
    padding: 40px;
    cursor: pointer;
    transition: all 0.3s ease;
    background-color: #f8f9fa;

    &:hover {
      background-color: #e9f3ff;
    }
  }

  .upload-content {
    display: flex;
    align-items: center;
    color: #6c757d;
    justify-content: space-between;
  }

  .preview-box {
    text-align: center;

    img {
      max-width: 200px;
      border-radius: 10px;
      margin-top: 10px;
    }
  }
}
